<template>
	<div>
		<h6>Los campos en (*) son obligatorios</h6>

		<b-form>

			<b-form-group label="*Reparto"
						  label-for="id_reparto"
						  @submit.stop.prevent="handleSubmit">
				<v-autocomplete id="id_reparto"
							   v-model="stocks.id_reparto"
							   type="text"
							   placeholder="Ingrese un Numero"
							   invalid-feedback="Complete este campo"
							   required
							   solo
							   filled
							   :items="op_repartos">
				</v-autocomplete>
				<b-form-invalid-feedback id="producto-live-feedback">
					{{ validacion.id_reparto.mensaje }}
				</b-form-invalid-feedback>
			</b-form-group>

			<b-form-group label="*Fecha" label-for="fechastock">
				<b-form-input id="fechastock"
							  v-model="stocks.fecha"
							  type="date"
							  placeholder="Ingrese la fecha del stock"
							  invalid-feedback="Complete este campo"
							  required>
				</b-form-input>
				<b-form-invalid-feedback id="nombre-live-feedback">
					{{ validacion.nombre.mensaje }}
				</b-form-invalid-feedback>
			</b-form-group>

			<b-form-group label="*Nombre" label-for="nombre">
				<b-form-input id="nombre"
							  v-model="stocks.nombre"
							  type="text"
							  placeholder="Ingrese el nombre del stock"
							  invalid-feedback="Complete este campo"
							  :state="validacion.nombre.estado"
							  required>
				</b-form-input>
				<b-form-invalid-feedback id="nombre-live-feedback">
					{{ validacion.nombre.mensaje }}
				</b-form-invalid-feedback>
			</b-form-group>

			<!----------    Componentes         -------------->
			<div v-for="(item, index) in stocks.componentes" :key="index">
				<b-form>
					<div class="d-flex  justify-content-between">

						<h4>Producto: {{ index + 1 }}</h4>
						<b-button variant="danger"
								  @click="eliminarProducto(index)"
								  style="color: white">
							<v-icon style="color: white"> mdi-delete </v-icon>
						</b-button>
					</div>
					<b-form-group label="*Producto"
								  label-for="producto"
								  @submit.stop.prevent="handleSubmit">
						<v-autocomplete id="producto"
									   :state="validacion.componentes[index].producto.estado"
									   v-model="item.producto"
									   type="text"
									   placeholder="Ingrese un Numero"
									   invalid-feedback="Complete este campo"
									   required
										solo
										filled
									   :items="op_productos">
						</v-autocomplete>
						<b-form-invalid-feedback id="producto-live-feedback">
							{{ validacion.componentes[index].producto.mensaje }}
						</b-form-invalid-feedback>
					</b-form-group>

					<b-form-group label="*Cantidad" label-for="cantidad">
						<b-form-input id="cantidad"
									  :state="validacion.componentes[index].cantidad.estado"
									  v-model="item.cantidad"
									  type="number"
									  placeholder="*Ingrese la cantidad"
									  invalid-feedback="Complete este campo"
									  required
									  @change="checkearstock(item.producto, item.cantidad, index)">
						</b-form-input>
						<b-form-invalid-feedback id="cantidad-live-feedback">
							{{ validacion.componentes[index].cantidad.mensaje }}
						</b-form-invalid-feedback>
					</b-form-group>
				</b-form>
				<b-button class="my-3"
						  variant="success"
						  @click="sumarProductos()"
						  style="color: white">+</b-button>
			</div>
		</b-form>

		<b-button class="mt-2" variant="success" block @click="putStock()">Guardar</b-button>
	</div>
</template>

<script>
	import { APIControler } from "@/store/APIControler";
    import axios from "axios";

	export default {
		props: {
            stocks: {},
			updateTable: Function,
		},
		data() {
			return {
				data: {},
				list_productos: {},
				text: "",
				op_productos: [],
                listaproduct: [],
				stockviejo: [],
				productoelegido: {},
                op_repartos: [],
				validacion: {
                    nombre: { estado: null, mensaje: "" },
                    fechastock: { estado: null, mensaje: "" },
                    id_reparto: { estado: null, mensaje: "" },
					componentes: [],
				},
			};
		},

		methods: {
			async resetForm() {
				this.stocks.nombre = "";
				this.stocks.componentes = null;
			},
            //Obtengo los productos
            async getProductos() {
                let productosAPI = new APIControler();
                productosAPI.apiUrl.pathname = "productos/";
                this.listaproduct = await productosAPI.getData();
                let option
                this.listaproduct.forEach((element) => {
                    option = {};
                    option.value =
                        "http://93.188.162.174:8081/productos/" + element.id_producto + "/";
                    option.text = element.nombre + ' - Stock: ' + element.stocktotal;
                    console.log(option);
                    this.op_productos.push(option);
                });
            },
            async getRepartos() {
                let mutualesAPI = new APIControler();
                mutualesAPI.apiUrl.pathname = "repartos/";
                this.data = await mutualesAPI.getData(this.list_repartos);
                this.data.forEach((element) => {
                    let option = {};
                    option.value =
                        "http://93.188.162.174:8081/repartos/" + element.id_reparto + "/";
                    option.text =
                        element.id_reparto +
                        "-- " +
                        element.nombre
                    console.log(option);
                    this.op_repartos.push(option);
                });
            },
			async putStock() {
				var id = this.stocks.id_stock;
				try {
					this.respuesta = await axios.put(
						"http://93.188.162.174:8081/stocks/" + id + "/",
						this.stocks
					);
                    this.stocks.componentes.forEach(element => {
                        console.log("Longitud");
                        this.actualizarStockProducto(element.producto.split('/')[4], element.cantidad);
                    })
					swal("Carga Exitosa", " ", "success");

				} catch (error) {
					const mje = error.response.status < 500
						? "Los datos no son válidos"
						: "Se ha detectado un problema ";
					swal("¡ERROR!", mje, "error");
					this.respuesta = error.response.data;
					console.log(error);
				}
				this.cargarFeedback();
				this.updateTable();
			},

			cargarFeedback() {
				let valido;
				if (!this.respuesta) this.respuesta = {};
				for (let key in this.validacion) {
					valido = !this.respuesta.hasOwnProperty(key);
					this.validacion[key].estado = valido;
					console.log(key);

					if (!valido) this.validacion[key].mensaje = this.respuesta[key][0];
				}
				this.cargarFeedbackComponentes()
			},
			cargarFeedbackComponentes() {
				let valido;
				let ok = false
				if (!this.respuesta.componentes) ok = true;
				this.validacion.componentes.forEach((b, index) => {

					for (let key in b) {
						valido = ok || !this.respuesta.componentes[index].hasOwnProperty(key);
						b[key].estado = valido;
						console.log(key);
						if (!valido) b[key].mensaje = this.respuesta.componentes[index][key][0];
					}
				});

			},
            async sumarProductos() {
                this.btn_producto = true;
                // this.resetFormAdh();
                // this.cantidad=this.cantidad +1;
                this.stocks.componentes.push({
                    cantidad: 0,
                    producto: null,
                });
                this.validacion.componentes.push({
                    cantidad: { estado: null, mensaje: "" },
                    producto: { estado: null, mensaje: "" },
                });
            },
            async eliminarProducto(index) {
                if (index == 0) {
                    alert("debe haber al menos un producto en el stock")
                } else {
                    this.stocks.componentes.splice(index, 1)
                    this.validacion.componentes.splice(index, 1)
                }
			},
            getProducto(id) {
                let producto = this.listaproduct.find(s => {
                    return s.id_producto == id
                })
                return producto;
            },
            getStockViejo(id_producto) {
                let idusado = 0;
                let retorno = 0
                this.stockviejo.forEach((element) => {
                    idusado = parseInt(element.producto.split('/')[4])
                    if (idusado == id_producto) {
                        retorno = parseInt(element.cantidad)
                    }
                });
                return retorno;
            },
            async actualizarStockProducto(id, stock) {
                console.log(this.getProducto(id));
                let productofinal = this.getProducto(id);
                this.productoelegido.id_producto = productofinal.id_producto
                this.productoelegido.nombre = productofinal.nombre
                this.productoelegido.tipo_producto = productofinal.tipo_producto
                this.productoelegido.letra = productofinal.letra
                this.productoelegido.numero_producto = productofinal.numero_producto
                this.productoelegido.codigo_unico = productofinal.codigo_unico
                let idnumer = parseInt(id);
                console.log('stock viejo')
                console.log(this.getStockViejo(idnumer))
                this.productoelegido.stocktotal = parseInt(productofinal.stocktotal) + parseInt(this.getStockViejo(idnumer)) - parseInt(stock)
                this.productoelegido.precioref = productofinal.precioref
                console.log(this.productoelegido)
                await axios
                    .put(
                        "http://93.188.162.174:8081/productos/" +
                        this.productoelegido.id_producto +
                        "/",
                        this.productoelegido
                    );
			},
            saveStocksViejos() {
                console.log(this.stocks.componentes);
                for (var i = 0; i < this.stocks.componentes.length; i++) {
                    let infoStock = {};
                    infoStock.producto = this.stocks.componentes[i].producto;
                    infoStock.cantidad = this.stocks.componentes[i].cantidad;
                    this.stockviejo.push(infoStock);
                }
                console.log('hola');
                console.log(this.stockviejo);
            },
			inicializar() {
				console.log("fuera del for!")
				for (let i = 0; i < this.stocks.componentes.length; i++) {
					console.log("iniciando!")
					this.validacion.componentes.push({
						cantidad: { estado: null, mensaje: "" },
						producto: { estado: null, mensaje: "" },
					});
				}
			}
		},
		beforeMount() {
			//this.getID();
			this.getProductos();
			this.inicializar();
			this.getRepartos();
            this.saveStocksViejos();
		},

	};
</script>

<style></style>
